(function() {
  'use strict';

  angular
    .module('angularRails')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('angularRails')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('angularRails')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 1;
      }

      return $http.get(apiHost + '/contributors?per_page=' + 1)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('angularRails')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1456034446982;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function() {
  'use strict';

  angular.module('angularRails')
    .factory('Articles', function ($resource) {
      return $resource('api/quotes/:articleId', {
        articleId: '@id'
      }, {
        update: {
          method: 'PUT'
        }
      });
    });
})();

(function() {
  'use strict';

  angular.module('angularRails')
    .controller('ArticlesController', function ($http, $mdSidenav, $mdToast, $log, Articles) {
      var vm = this;
      Articles.query(function (res) {
        vm.articles = res;
      });
       

    vm.showSuccess = function($event) {
      $mdToast.showSimple('Quote saved! May the luck be ever in your favor (to see it).');
    };


    vm.showError = function($event) {
      $mdToast.showSimple('Sorry... Something wrong with the Ruby guys. I will kick his ass!');
    };

    vm.showInvalid = function($event) {
      $mdToast.showSimple("Sorry. You quote's length should be from 0 - 140 characters");
    };

    function toggleUsersList() {
      $mdSidenav('left').toggle();
    }

    vm.toggleList = toggleUsersList;
    
    vm.submitForm = function(quote) {
      $log.debug("posting data....");
      $log.debug('toggle left is done');
      if (quote == null || quote.content == null || quote.content.length == 0 || quote.content.length > 140) {
        vm.showInvalid();
      } else {
        $http.post('/api/quotes', angular.toJson(quote)).success(function(){
          vm.showSuccess()
        }).error(function(){
          vm.showError()
        });
      }
     };
    });
})();

